import * as types from "./mutations_types"

export default {
  [types.IS_REFRESH](state){
        state.is_refresh = !state.is_refresh
    },
    [types.SET_CURRENTROW](state,data){
        state.currentRow = data
    },
    [types.SET_EXPAND_KEYS](state,data){
        state.expandKeys = data
    }
}


